import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Card from "./Card";
import "./../utils/painter.css";

const Container = styled.div`
  position: relative;
  height: 100vh;
  width: 100%;
`;

const Image = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const ContentContainer = styled.div`
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
`;

const Title = styled.h2`
  font-size: 3rem;
  color: white;
  margin-left: 1rem;
  margin-bottom: 0.5rem;
  font-family: "Pacifico";
`;

const Description = styled.p`
  font-size: 3rem;
  color: white;
  margin-left: 1rem;
  margin-bottom: 0.5rem;
  font-family: "Pacifico";
`;

const Overlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.5;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
`;

const OverlayMessage = styled.h1`
  color: white;
  font-size: 3rem;
`;

function CardContainer(props) {
  const [currentCard, setCurrentCard] = useState(0);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    function handleOrientationChange(event) {
      if (event.beta > 45) {
        setCurrentCard(1);
      } else {
        setCurrentCard(0);
      }
    }

    const userAgent =
      typeof window.navigator === "undefined" ? "" : navigator.userAgent;
    const isMobile = Boolean(
      userAgent.match(
        /Android|BlackBerry|iPhone|iPad|iPod|Opera Mini|IEMobile|WPDesktop/i
      )
    );
    setIsMobile(isMobile);

    window.addEventListener("deviceorientation", handleOrientationChange);
    return () => {
      window.removeEventListener("deviceorientation", handleOrientationChange);
    };
  }, []);

  return (
    <div>
      {isMobile ? (
        <div className="card-container">
          <div onClick={() => setCurrentCard(currentCard === 0 ? 1 : 0)}>
            <Card
              image={props.cardData[currentCard].image}
              title={props.cardData[currentCard].title}
              description={props.cardData[currentCard].description}
            />
          </div>
        </div>
      ) : (
        <Overlay>
          <OverlayMessage>
            Please switch to a mobile device to view this content
          </OverlayMessage>
        </Overlay>
      )}

      {!isMobile && (
        <Container>
          <Image src={props.cardData[currentCard].image} />

          <ContentContainer>
            <Title>{props.cardData[currentCard].title}</Title>
            <Description>{props.cardData[currentCard].description}</Description>
          </ContentContainer>
        </Container>
      )}
    </div>
  );
}

export default CardContainer;
